import React from 'react'
import clsx from 'clsx'
import {
    makeStyles,
    Avatar,
    Card,
    CardHeader,
    Typography,
} from '@material-ui/core/'
import {
  Highlight,
} from 'react-instantsearch-dom'

const useStyles = makeStyles(theme => ({
	hit: {
	},
}))

export default function Hit(props) {
	
	const classes = useStyles()
	const { hit } = props
	const {
		// title,
		image,
		breeder,
	} = hit
	// console.log ('title', title)
    React.useEffect(() => {
	}, [])

	return	<Card className={ clsx ( classes.hit ) }>
				<CardHeader 
					disableTypography
					title={ <Typography variant={ `body1` }><Highlight 
								attribute={ `title` } 
								hit={ props.hit }
								nonHighlightedTagName={`span`}
								tagName={`mark`}
							/></Typography> }
					subheader={ <Typography variant={ `body2` }>
									{ breeder }
								</Typography> }
					avatar={ <Avatar src={ image } /> }
				/>
				
				
			</Card>
}

