import React from 'react'
import clsx from 'clsx'
import { useSelector } from 'react-redux'
import { 
	toggleImport,
	initBus,
} from '../redux/admin/actions'
import {
    makeStyles,
    Avatar,
    CardHeader,
    CardContent,
    Dialog,
    DialogContent,
    Typography,
    IconButton,
    Grid,
    Link,
    LinearProgress,
} from '@material-ui/core/'
import { Seed } from './' 
import  { Icon } from '../theme'

const useStyles = makeStyles((theme) => ({
	importCard: {
		background: 'rgba(255, 255, 255, 0.1)',
	},
	closeBtn:{
		marginTop: -theme.spacing(2),
	},
	progress: {
		height: 5,
		marginBottom: theme.spacing(),
	},
	horzPad:{
		paddingLeft: theme.spacing(3),
		paddingRight: theme.spacing(3),
	},
	grow:{
		flexGrow: 1,
	},
	link:{
		cursor: 'pointer',
		
	},
	title:{
		display: 'flex',
	},
	form: {
		display: 'flex',
		flexDirection: 'column',
		margin: 'auto',
		width: 'fit-content',
	},
	formControl: {
		marginTop: theme.spacing(2),
		minWidth: 120,
	},
	formControlLabel: {
		marginTop: theme.spacing(),
	},
	btnText: {
		marginLeft: theme.spacing(2),
		marginRight: theme.spacing(2),
	}
}))

    const closeAdmin = (e) => {
    	// console.log ('closeAdmin')
    	e.preventDefault()
		toggleImport( false )
    }

export default function Import() {

	const classes = useStyles()
	const adminSlice = useSelector(state => state.admin)
   	const { 
		importOpen,
		bus,
    	source,
    	languages,
    	queue,
	} = adminSlice

	let loadingLangs = []
	let allComplete = true
	for( let key in languages ) {
	    const value = languages[key]
	    let isComplete = true
	    if (bus){
	    	if ( !bus[key].complete) {
	    		isComplete = false
	    		allComplete = false
	    	}
	    }
	    loadingLangs.push(	<div key={ `language_${key}` }>
	    						<Typography variant={ `body1` }>
	    							{ isComplete ? null : ` Loading... ` }
									{ value.native } {` `}
									<Link
		    							className={ clsx( classes.link ) }
		    							onClick={ (e)=> {
		    								e.preventDefault()
		    								const url = `${ source.url }${ key }.xml`
		    								window.open(url, `_blank`)
		    							}}>
		    							{ source.url }{ key }.xml
		    						</Link>
		    						{ !isComplete ? null : ` loaded.` }
								</Typography>					    		
					    	</div>
					    )
	}
	if (allComplete) loadingLangs = []

	let id = null
	if (queue[0]){
		id = queue[0].id
	}

	const output =	<div className={ clsx( classes.importCard )}>
						<CardHeader 
							disableTypography
							action={<IconButton
						                color={ `primary` }
						                onClick={ closeAdmin }>
						                <Icon icon={ `close` } color={ `primary` } />
						            </IconButton>}
							avatar={ <IconButton
                                          color={ `primary` }
                                          onClick={ (e) => {
                                            e.preventDefault()
                                            const url = `https://sensiseeds.com/en/`
                                            window.open( url, `_blank`)
                                          }}>
                                        <Avatar src={ source.image }/>
                                      </IconButton> }
							title={ <Typography variant={ `h6` }>												
										<Link
			    							className={ clsx( classes.link ) }
			    							onClick={ (e)=> {
			    								e.preventDefault()
			    								const url = `https://sensiseeds.com/en/`
			    								window.open(url, `_blank`)
			    							}}>
				    							{ source.title }
				    					</Link>
									</Typography> }
							subheader={ <React.Fragment>
											{ queue.length ? <Typography variant={ `body1` }>
												{ queue.length } in queue
											</Typography> : null }
										</React.Fragment>}
							/>								
						<CardContent>
							{ queue.length ? <React.Fragment>
												<Seed seedId={ id } />
											</React.Fragment> : null }
							{ loadingLangs }
						</CardContent>
					</div>

    React.useEffect(() => {
    	const {
    		bussing,
    		bussed,
    	} = adminSlice
    	if ( !bussing && !bussed && importOpen ) initBus()
	}, [adminSlice])



	return	<Dialog 
				fullScreen
				// fullWidth
    			// maxWidth={ `md` }
				open={ importOpen }
        		onClose={ closeAdmin }>
        		<div className={ clsx( classes.progress ) }>
					{ allComplete ? null : <LinearProgress /> }
				</div>
				<DialogContent>
					<Grid container className={ clsx( classes.title )}>
						<Grid item xs={ 12 }>
							{ output }
						</Grid>
					</Grid>
				</DialogContent>
			</Dialog>
}

