import { createAction } from '@reduxjs/toolkit'
import { getStore } from '../../'
import axios from 'axios'
import parser from 'fast-xml-parser'


export const error = createAction(`ADMIN/ERROR`)
export const importOpen = createAction(`ADMIN/IMPORTOPEN`)
export const managerOpen = createAction(`ADMIN/MANAGEROPEN`)
export const bus = createAction(`ADMIN/BUS`)
export const bussing = createAction(`ADMIN/BUSSING`)
export const bussed = createAction(`ADMIN/BUSSED`)
export const queue = createAction(`ADMIN/QUEUE`)
export const saving = createAction(`ADMIN/SAVING`)
export const auto = createAction(`ADMIN/AUTO`)

export const save = seed => {
	const store = getStore()
	const endpoint = `${process.env.REACT_APP_LISTINGSLAB_API}/canna`
	//console.log ( 'save', endpoint, seed )
	const requestBody = { 
		action: `save`,
		seed 
	}
	store.dispatch({type: `ADMIN/SAVING`, saving: true })
	axios.post(endpoint, requestBody)
		.then(function(response) {
			const {
				status,
				message,
			} = response.data.response.data
			if (status === 200){
				console.log ( status, message )
				skip()
			}
			store.dispatch({type: `ADMIN/SAVING`, saving: false })
			return true
		})
		.catch(function(error) {
			store.dispatch({type: `ADMIN/ERROR`, error })
			store.dispatch({type: `ADMIN/SAVING`, saving: false })
			return false
		})

	return true
}

export const skip = () => {
	const store = getStore()
	let newQ = store.getState().admin.queue
	newQ = newQ.slice( 1, newQ.length )
	store.dispatch({ type: `ADMIN/QUEUE`, queue: newQ })
	return true
}

export const getById = id => {
	const store = getStore()
	const bus = store.getState().admin.bus
	if (!bus) return false
	let seed = { en: null, nl: null, fr: null, es: null, de: null }
	if (!bus.en.data || !bus.nl.data || !bus.fr.data || !bus.es.data || !bus.de.data ) return false
	const englishList = bus.en.data.item
	for (let i = 0; i< englishList.length; i++){
		if (englishList[i].id === parseFloat(id)){
			seed.en = englishList[i]
		}
	}
	const dutchList = bus.nl.data.item
	for (let i = 0; i< dutchList.length; i++){
		if (dutchList[i].id === parseFloat(id)){
			seed.nl = dutchList[i]
		}
	}
	const frenchList = bus.fr.data.item
	for (let i = 0; i< frenchList.length; i++){
		if (frenchList[i].id === parseFloat(id)){
			seed.fr = frenchList[i]
		}
	}
	const spanishList = bus.es.data.item
	for (let i = 0; i< spanishList.length; i++){
		if (spanishList[i].id === parseFloat(id)){
			seed.es = spanishList[i]
		}
	}
	const germanList = bus.de.data.item
	for (let i = 0; i< germanList.length; i++){
		if (germanList[i].id === parseFloat(id)){
			seed.de = germanList[i]
		}
	}
	return seed
}

export const initBus = () => {
	const store = getStore()
	store.dispatch( { type: `ADMIN/BUSSING`, bussing: true } )
	const {
		source,
		languages,
	} = store.getState().admin
	const bus = {}
	for(let key in languages) {
	    const value = languages[key]
	    if (value){
	    	bus[key] = {
	    		endpoint: `${source.url}${key}.xml`,
	    		data: null,
	    		complete: false,
	    	}
	    }
	}
	store.dispatch({type: `ADMIN/BUS`, bus })
	for(let key in bus) {
		const source = bus[key]
		axios.get(source.endpoint)
			.then(function(response) {
				// console.log ('response', response)
				const queue = parser.parse(response.data).rss.channel.item
				let formattedQ = []
				if ( key === `en` ){
					for ( let i = 0; i < queue.length; i ++ ){
						formattedQ.push({
							id: queue[i].id,
							url: queue[i].url,
						})
					}
					store.dispatch({ type: `ADMIN/QUEUE`, queue: formattedQ })
				}
				
				const oldBus = getStore().getState().admin.bus
				const newBus = {
					...oldBus,
					[key]: {
						...oldBus[key],
						data: parser.parse(response.data).rss.channel,
			    		complete: true,
			    	}
				}
				
				store.dispatch({type: `ADMIN/BUS`, bus: newBus })
				store.dispatch({type: `ADMIN/BUSSING`, bussing: false })
				store.dispatch({type: `ADMIN/BUSSED`, bussed: true })
				return true
			})
			.catch(function(error) {
				store.dispatch({type: `ADMIN/ERROR`, error })
				store.dispatch({type: `ADMIN/BUSSING`, bussing: false })
				store.dispatch({type: `ADMIN/BUSSED`, bussed: true })
				return false
			})
		}
}

export const toggleManager = bool => {
	const store = getStore()
	store.dispatch({type: `ADMIN/MANAGEROPEN`, managerOpen: bool })
	return true
}

export const toggleImport = bool => {
	const store = getStore()
	store.dispatch({type: `ADMIN/IMPORTOPEN`, importOpen: bool })
	return true
}
