import React from 'react'
// import clsx from 'clsx'
import { useSelector } from 'react-redux'
import { 
  themeDark, 
  themeLight, 
} from './theme'
import {
  // makeStyles,
  MuiThemeProvider, 
  createMuiTheme,
} from '@material-ui/core/'
import {
  Basic,
} from './components'

// const useStyles = makeStyles((theme) => ({
//   app: {
//     width: '100%',
//     maxHeight: 500,
