import React from 'react'
import { useSelector } from 'react-redux'
import { 
  toggleAppMenu,
  toggleFullScreen,
  toggleThemeMode,
} from '../redux/app/actions'
import { 
  toggleImport,
} from '../redux/admin/actions'
import {
    makeStyles,
    withStyles,
    useTheme,
    Menu,
    MenuItem,
    IconButton,
    ListItemIcon,
    ListItemText,
    // Tooltip,
} from '@material-ui/core/'
import  { Icon } from '../theme'

const useStyles = makeStyles((theme) => ({
  thingo: {
    marginRight: theme.spacing(2),
  },
}))

const StyledMenu = withStyles({
  paper: {
    border: '1px solid rgba(255, 255, 255, 0.2)',
    // border: '1px solid #d3d4d5',
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
))

const StyledMenuItem = withStyles((theme) => ({
  root: {
    '&:focus': {
      // backgroundColor: theme.palette.primary.main,
      '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
        // color: theme.palette.common.white,
      },
    },
  },
}))(MenuItem)

export default function AppMenu() {

  const classes = useStyles()
  const theme = useTheme()
  const primary = theme.palette.primary.main
  const [anchorEl, setAnchorEl] = React.useState(null)

  const appSlice = useSelector(state => state.app)
  const {
    themeMode,
  } = appSlice

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
    toggleAppMenu( true )
  }

  const handleClose = () => {
    setAnchorEl( null )
    toggleAppMenu( false )
  }

  return <div>
          
            <IconButton 
              style={{ marginLeft: 8, marginRight: 8 }}
              edge={ `end` }
              aria-haspopup={ true }
              color={ `secondary` }
              onClick={ handleClick }>
              <Icon icon={ `logo` } color={ primary } />
            </IconButton>
          

          <StyledMenu
            id={ `appMenu` }
            anchorEl={ anchorEl }
            keepMounted
            open={Boolean( anchorEl )}
            onClose={ handleClose }
          >

            <StyledMenuItem
              onClick={(e) => {
                e.preventDefault()
                toggleImport( true )
                handleClose()
              }}>
              <ListItemIcon>
                <Icon icon={ `import` } color={ `secondary` } />
              </ListItemIcon>
              <ListItemText 
                className={ classes.thingo} 
                primary={ `Import` }
              />
            </StyledMenuItem>

           <StyledMenuItem
              onClick={(e) => {
                e.preventDefault()
                toggleFullScreen( true )
                handleClose()
              }}>
              <ListItemIcon>
                <Icon icon={ `fullscreen` } color={ `secondary` } />
              </ListItemIcon>
              <ListItemText 
                className={ classes.thingo} 
                primary={ `Full Screen` }
              />
            </StyledMenuItem> 



             <StyledMenuItem
              onClick={(e) => {
                e.preventDefault()
                toggleThemeMode( !themeMode )
                handleClose()
              }}>
              <ListItemIcon>
                <Icon icon={ themeMode === `dark` ? `light` : `dark` } color={ `inherit` } />
              </ListItemIcon>
              <ListItemText 
                className={ classes.thingo} 
                primary={ !themeMode ? `Dark Mode` : `Light mode` }
              />
            </StyledMenuItem> 

          </StyledMenu>
        </div>
}
