import { createReducer } from '@reduxjs/toolkit'
import {
  error,
  importOpen,
  managerOpen,
  bus,
  bussing,
  bussed,
  queue,
  saving,
  auto,
} from "./actions"

export const adminSlice = {
  error: null,
  auto: false,
  importOpen: false,
  managerOpen: false,
  bus: null,
  bussing: false,
  bussed: false,
  queue: [],
  saving: false,
  languages: { 
    en: {
      flag: `/svg/flags/gb.svg`,
      native: `English`,
      international: `English`,
    }, 
    nl: {
      flag: `/svg/flags/nl.svg`,
      native: `Nederlands`,
      international: `Dutch`,
    },  
    fr: {
      flag: `/svg/flags/fr.svg`,
      native: `Franais`,
      international: `French`,
    }, 
    es: {
      flag: `/svg/flags/es.svg`,
      native: `Espagnol`,
      international: `Spanish`,
    }, 
    de: {
      flag: `/svg/flags/de.svg`,
      native: `Deutsch`,
      international: `German`,
    },
  },
  source: {
    "name": "sensiseeds",
    "title": "Sensi Seeds",
    "image": "/svg/sensiseeds.svg",
    "url": "/xml/sensiseeds/"
  }
}
const adminReducer = createReducer(adminSlice, {

  [auto]: (state, action) => {
    state.auto = action.auto
    return state
  },

  [saving]: (state, action) => {
    state.saving = action.saving
    return state
  },

  [queue]: (state, action) => {
    state.queue = action.queue
    return state
  },
  
  [managerOpen]: (state, action) => {
    state.managerOpen = action.managerOpen
    return state
  },
  
  [bussed]: (state, action) => {
    state.bussed = action.bussed
    return state
  },
  
  [bussing]: (state, action) => {
    state.bussing = action.bussing
    return state
  },

  [bus]: (state, action) => {
    state.bus = action.bus
    return state
  },

  [importOpen]: (state, action) => {
    state.importOpen = action.importOpen
    return state
  },

  [error]: (state, action) => {
    state.error = action.error
    return state
  },

})

export { adminReducer }