import React from 'react'
import { useSelector } from 'react-redux'
import clsx from 'clsx'
import {
    makeStyles,
    Avatar,
    Button,
    CardHeader,
    CardContent,
    CardActions,
    Tabs,
    Tab,
    Typography,
    IconButton,
    Grid,
} from '@material-ui/core/'
import { 
  getById,
  skip,
  save,
} from '../redux/admin/actions'
import { Icon } from '../theme'

const useStyles = makeStyles((theme) => ({
  seed: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
  },
  btnTxt:{
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
  },
  seedCard:{
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
  tabLabel: {
    width: '100%',
    textAlign: 'left',
    // border: `1px solid ${theme.palette.divider}`,
  },
  tabLabelAvatar: {
    width: 24,
    height: 24,
    marginLeft: theme.spacing(),
    marginRight: theme.spacing(),
  },
  seedAvatar: {
    width: 75,
    height: 75,
  },
  localeDescription:{
    maxWidth: 360,
  },
  grow: {
    flexGrow: 1
  },
}))


function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  }
}

function TabPanel(props) {
  const classes = useStyles()

  const adminSlice = useSelector(state => state.admin)
  const {
    queue,
  } = adminSlice
  let id = 0
  if (queue[0].id){
    id = queue[0].id
  }
  let seed = getById(id)
  if (!seed.en) return null
  const { children, value, index, ...other } = props
  let locale = ``
  switch ( value ) {
      case 0:
        locale = `en`
        break
      case 1:
        locale = `nl`
        break
      case 2:
        locale = `fr`
        break
      case 3:
        locale = `es`
        break
      case 4:
        locale = `de`
        break
      default: {
          locale = `en`
      }
  }

  seed = {
    title: seed.en.title,
    url: seed.en.url,
    image: seed.en.image,
    breeder: `sensiseeds`,
    sku: seed.en.id,
    strainType: seed.en.strain_type,
    price: seed.en.price,
    numberSeeds: seed.en.number_of_seeds,
    currency: seed.en.currency,
    locale:{
      ...seed,
    }
  }

  const localeData = seed.locale[locale]
  const localeDescription = `${localeData.description.substring(0, 250)}...`

  return <div
      role="tabpanel"
      hidden={ value !== index }
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && <div className={ clsx (classes.seedCard) }>
                              
                              <CardHeader 
                                disableTypography
                                avatar={ <IconButton
                                          color={ `primary` }
                                          onClick={ (e) => {
                                            e.preventDefault()
                                            window.open( localeData.url, `_blank`)
                                          }}>
                                          <Avatar 
                                            className={ clsx( classes.seedAvatar ) }
                                            src={ localeData.image } />
                                          </IconButton> }
                                title={ <Typography variant={ `h6` } >
                                          { localeData.title }
                                        </Typography>}
                                subheader={ <Typography variant={ `body2` } >
                                          { localeData.meta_title }
                                        </Typography>}
                                
                              />
                              <CardContent>

                                <Typography variant={ `body1` } gutterBottom>
                                  { localeData.category }&nbsp;
                                  { localeData.strain_type }&nbsp;
                                  { localeData.climate_zone }&nbsp;
                                  { localeData.yield }&nbsp;
                                  { localeData.heigh_gain }
                                </Typography>

                                <Typography 
                                  gutterBottom
                                  variant={ `body2` } 
                                  className={ clsx( classes.localeDescription ) }>
                                  { localeDescription }
                                </Typography>

                                <Typography variant={ `button` } gutterBottom>
                                  { localeData.currency.toUpperCase() }&nbsp;
                                  { localeData.price } / { localeData.number_of_seeds } Seeds
                                </Typography>
                              
                                

                              </CardContent>

                              <CardActions>
                                
                                

                                <Button
                                  variant={ `contained` }
                                  color={ `primary` }
                                   onClick={ (e) => {
                                      e.preventDefault()
                                      save ( seed )
                                    }}>
                                    <Icon icon={ `save` } color={ `inherit` } />
                                   <span className={ clsx (classes.btnTxt) }>
                                     Save
                                   </span>
                                   
                                </Button>

                                <Button
                                  variant={ `text` }
                                  color={ `primary` }
                                  onClick={ (e) => {
                                    e.preventDefault()
                                    skip (id)
                                  }}>
                                   
                                   <span className={ clsx (classes.btnTxt) }>
                                     Skip
                                   </span>
                                   <Icon icon={ `skip` } color={ `inherit` } />
                                </Button>



                              </CardActions>

                            </div>}
    </div>
}

export default function Seed(props) {
  const classes = useStyles()
  const [value, setValue] = React.useState(0)
  const adminSlice = useSelector(state => state.admin)
  React.useEffect(() => {
    //console.log ('do as I say')
      // const {
      //   auto,
      // } = adminSlice
      // if (auto) 
      // console.log ('auto', auto)
  }, [ adminSlice ])
  
  // const { seedId } = props
  
  const {
    queue,
  } = adminSlice
  let id = 0 
  if (queue[0].en){
    id = queue[0].en.id
  }
  const seed = getById(id)
  if ( !seed ) return null

  



  const handleChange = (event, newValue) => {
    setValue(newValue)
  }
 
  return <div className={ clsx (classes.seed) }>

        

      <Tabs
        orientation={ `vertical` }
        variant={ `scrollable` }
        value={ value }
        onChange={ handleChange }
        aria-label="Seed Locales"
        className={classes.tabs}>
        
        <Tab label={
          <div className={ clsx( classes.tabLabel ) }>
            <Grid container>
              <Grid item>
                <Avatar 
                  src={ `/svg/flags/gb.svg` }
                  className={ clsx( classes.tabLabelAvatar ) }
                />
              </Grid>
              <Grid item>{`English`}</Grid>
            </Grid>
          </div>
        } {...a11yProps(0)} />

        <Tab label={
          <div className={ clsx( classes.tabLabel ) }>
            <Grid container>
              <Grid item>
                <Avatar 
                  src={ `/svg/flags/nl.svg` }
                  className={ clsx( classes.tabLabelAvatar ) }
                />
              </Grid>
              <Grid item>{`Nederlands`}</Grid>
            </Grid>
          </div>
        }{...a11yProps(1)} />
        <Tab label={
          <div className={ clsx( classes.tabLabel ) }>
            <Grid container>
              <Grid item>
                <Avatar 
                  src={ `/svg/flags/fr.svg` }
                  className={ clsx( classes.tabLabelAvatar ) }
                />
              </Grid>
              <Grid item>{`Franais`}</Grid>
            </Grid>
          </div>
        }{...a11yProps(2)} />
        <Tab label={
          <div className={ clsx( classes.tabLabel ) }>
            <Grid container>
              <Grid item>
                <Avatar 
                  src={ `/svg/flags/es.svg` }
                  className={ clsx( classes.tabLabelAvatar ) }
                />
              </Grid>
              <Grid item>{`Espagnol`}</Grid>
            </Grid>
          </div>
        }{...a11yProps(3)} />
        <Tab label={
          <div className={ clsx( classes.tabLabel ) }>
            <Grid container>
              <Grid item>
                <Avatar 
                  src={ `/svg/flags/de.svg` }
                  className={ clsx( classes.tabLabelAvatar ) }
                />
              </Grid>
              <Grid item>{`Deutsch`}</Grid>
            </Grid>
          </div>
        }{...a11yProps(4)} />
      </Tabs>

      <TabPanel value={ value } index={ 0 } seed={ seed } />
      <TabPanel value={ value } index={ 1 } seed={ seed } />
      <TabPanel value={ value } index={ 2 } seed={ seed } />
      <TabPanel value={ value } index={ 3 } seed={ seed } />
      <TabPanel value={ value } index={ 4 } seed={ seed } />
        
    </div>
}

